import React, { useEffect, useState } from "react";
import {
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  Typography,
  Box,
  Pagination,
  Select,
  MenuItem,
  FormControl,
  InputLabel,
  Button,
  Tabs,
  Tab,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
} from "@mui/material";
import {
  CourierOrderCancel,
  generateOrderPickup,
  getCourierInvoice,
  getCourierLabel,
  getCourierMainifeast,
  getCourierOrder,
} from "../../api/login/Login";
import { IconButton, Menu } from "@mui/material";
import MoreVertIcon from "@mui/icons-material/MoreVert";
import toastMessage from "../../utils/toastMessage";
import { ToastContainer } from "react-toastify";
import { NavLink, useNavigate } from "react-router-dom";
import Loader from "../../common/loader/Loader";
import { message } from "antd";

export const statusFilters = {
  New: "New",
  "Ready To Ship": "Ready To Ship",
  Manifested: "Manifested",
  Canceled: "Cancel Order",
  // pending: 'Pending',
  // delivered: 'Delivered',
  // rto: 'RTO',
  // notPicked: 'Not Picked',
  // lost: 'Lost',
};

const OrderShowTable = () => {
  const today = new Date().toISOString().split("T")[0]; // YYYY-MM-DD
  const [page, setPage] = useState(1);
  const [rowsPerPage, setRowsPerPage] = useState(10);
  const [resetKey, setResetKey] = useState(false);
  const [totalData, setTotalData] = useState(0);
  const [loading, setLoading] = useState(false);
  const [openDialog, setOpenDialog] = useState(false);

  const navigate = useNavigate();

  const [filters, setFilters] = useState({
    startDate: today,
    endDate: today,
    orderId: "",
    status: "New",
  });

  const [anchorEl, setAnchorEl] = useState(null);
  const [selectedOrder, setSelectedOrder] = useState(null);
  const open = Boolean(anchorEl);

  const handleMenuClick = (event, order) => {
    setAnchorEl(event.currentTarget);
    setSelectedOrder(order);
  };

  const handleClose = () => {
    setAnchorEl(null);
    setSelectedOrder(null);
  };

  const genrateInvoice = async () => {
    let body = {
      shipRocket_order_id: selectedOrder?.shipRocket_order_id,
    };
    try {
      let res = await getCourierInvoice(body);
      if (res?.data?.error === false) {
        // Trigger download
        const link = document.createElement("a");
        link.href = res?.data?.data.invoice_url;
        link.download = res?.data?.data.invoice_url;
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
      }
      toastMessage(res?.data?.error, res?.data?.message);
      handleClose();
    } catch (error) {}
  };

  const genrateLabel = async () => {
    console.log("click");
    let body = {
      shipment_id: selectedOrder?.shipment_id,
    };
    try {
      let res = await getCourierLabel(body);
      console.log(res, "res");
      if (res?.data?.error === false) {
        // Trigger download
        const link = document.createElement("a");
        link.href = res?.data?.data.label_url;
        link.download = res?.data?.data.label_url;
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
      }
      toastMessage(res?.data?.error, res?.data?.message);
      handleClose();
    } catch (error) {}
  };

  const genrateMenifest = async (order) => {
    if (order?.manifest_url) {
      const link = document.createElement("a");
      link.href = order?.manifest_url;
      link.download = order?.manifest_url;
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
      return;
    }
    let body = {
      shipment_id: order?.shipment_id,
    };
    try {
      let res = await getCourierMainifeast(body);
      if (res?.data?.error === false) {
        // Trigger download
        const link = document.createElement("a");
        link.href = res?.data?.data.manifest_url;
        link.download = res?.data?.data.manifest_url;
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
      }
      toastMessage(res?.data?.error, res?.data?.message);
      handleClose();
      setResetKey((pre) => !pre);
    } catch (error) {}
  };

  const handleCancelClick = () => {
    setOpenDialog(true);
  };

  const handleDialogClose = () => {
    setOpenDialog(false);
  };

  const cancelOrder = async () => {
    let body = {
      shipRocket_order_id: selectedOrder?.shipRocket_order_id,
    };
    try {
      let res = await CourierOrderCancel(body);
      if (res?.data?.error === false) {
        handleDialogClose();
        handleClose();
        setResetKey((pre) => !pre);
      }
      toastMessage(res?.data?.error, res?.data?.message);
    } catch (error) {}
  };

  const handleShip = (item) => {
    // console.log(item , "item")
    navigate(
      `/order-ship/?delivery_postcode=${item?.shipping_address?.shipping_pincode}&order_id=${item?.shipRocket_order_id}`,
    );
  };

  const secdulePickup = async (item) => {
    let body = {
      shipment_id: item?.shipment_id,
    };
    setLoading(true);
    try {
      let res = await generateOrderPickup(body);
      if (res?.data?.error === false) {
        setResetKey((pre) => !pre);
      }
      toastMessage(res?.data?.error, res?.data?.message);
    } catch (error) {
    } finally {
      setLoading(false);
    }
  };

  const [orderList, setOrderList] = useState([]);

  const handleChangePage = (_, value) => {
    setPage(value);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(Number(event.target.value));
    setPage(1); // reset to first page when rowsPerPage changes
  };

  //   const paginatedOrders = orders.slice((page - 1) * rowsPerPage, page * rowsPerPage);

  const handleChange = (e) => {
    const { name, value } = e.target;
    if (name === "endDate" && filters.startDate && value < filters.startDate) {
      message.error("End date cannot be earlier than start date");
      return;
    }

    if (name === "startDate" && filters.endDate && value > filters.endDate) {
      message.error("Start date cannot be later than end date");
      return;
    }
    setFilters((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  const handleSubmit = () => {
    console.log("Selected Filters:", filters);
    setResetKey((pre) => !pre);
    // you can pass `filters` to your API or logic here
  };

  const handleReset = () => {
    setFilters({
      startDate: "",
      endDate: "",
      orderId: "",
      status: "New",
    });
    setActiveStatus("New");
    setPage(1);
    setRowsPerPage(10);
    setResetKey((pre) => !pre);
  };

  const getOrder = async () => {
    const body = {
      // ...(filters.status && { status: filters.status }),
      ...(filters.startDate && { start_date: filters.startDate }),
      ...(filters.endDate && { end_date: filters.endDate }),
      ...(filters.orderId && { order_id: filters.orderId }),
      page: page - 1,
      count: rowsPerPage,
      status: filters.status,
    };
    setLoading(true);
    try {
      let res = await getCourierOrder(body);
      if (res?.data?.error === false && res?.data?.statusCode === 200) {
        setOrderList(res?.data?.data);
        setTotalData(res?.data?.totalCount[0].count);
      } else {
        setOrderList([]);
        setTotalData(0);
      }
    } catch (error) {
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    getOrder();
  }, [page, rowsPerPage, resetKey]);

  useEffect(() => {
    handleReset();
  }, []);

  const [activeStatus, setActiveStatus] = useState("New");

  const handleStatusChange = (_, newValue) => {
    setFilters((pre) => ({ ...pre, status: newValue }));
    setActiveStatus(newValue);
    setResetKey((pre) => !pre);
    // ðŸ‘‡ trigger filtering logic here if needed
  };

  const totalPages = Math.ceil(totalData / rowsPerPage);

  const renderNoDataRow = (colSpan = 5) => (
    <TableBody>
      <TableRow>
        <TableCell colSpan={colSpan} align="center" sx={{ color: "gray" }}>
          No data found
        </TableCell>
      </TableRow>
    </TableBody>
  );

  const renderTableHeader = () => {
    switch (activeStatus) {
      case "New":
        return (
          <TableRow>
            <TableCell>
              <strong>S.No.</strong>
            </TableCell>
            <TableCell>
              <strong>Order Date</strong>
            </TableCell>
            <TableCell>
              <strong>Product Value</strong>
            </TableCell>
            <TableCell>
              <strong>E-Way Bill Mandatory</strong>
            </TableCell>
            <TableCell>
              <strong>Order ID</strong>
            </TableCell>
            {/* <TableCell><strong>Qty</strong></TableCell> */}
            {/* <TableCell><strong>Address</strong></TableCell> */}
            <TableCell>
              <strong>Order Status</strong>
            </TableCell>
            <TableCell>
              <strong>Action</strong>
            </TableCell>
          </TableRow>
        );
      case "Ready To Ship":
      case "Manifested":
        return (
          <TableRow>
            <TableCell>
              <strong>S.No.</strong>
            </TableCell>
            <TableCell>
              <strong>Order Date</strong>
            </TableCell>
            <TableCell>
              <strong>Product Value</strong>
            </TableCell>
            <TableCell>
              <strong>Order ID</strong>
            </TableCell>
            <TableCell>
              <strong>Courier Name</strong>
            </TableCell>
            <TableCell>
              <strong>Tracking Id</strong>
            </TableCell>
            <TableCell>
              <strong>Total Charges</strong>
            </TableCell>

            {/* <TableCell><strong>Qty</strong></TableCell> */}
            {/* <TableCell><strong>Address</strong></TableCell> */}
            <TableCell>
              <strong>Order Status</strong>
            </TableCell>
            <TableCell>
              <strong>Action</strong>
            </TableCell>
          </TableRow>
        );
      case "Canceled":
        return (
          <TableRow>
            <TableCell>
              <strong>S.No.</strong>
            </TableCell>
            <TableCell>
              <strong>Order Date</strong>
            </TableCell>
            <TableCell>
              <strong>Product Value</strong>
            </TableCell>
            <TableCell>
              <strong>Order ID</strong>
            </TableCell>
            <TableCell>
              <strong>Order Status</strong>
            </TableCell>
          </TableRow>
        );
      default:
        return null;
    }
  };

  const renderTableContent = () => {
    switch (activeStatus) {
      case "New":
        if (!orderList?.length) return renderNoDataRow(7);
        return (
          <TableBody>
            {orderList?.map((order, index) => (
              <TableRow key={index} hover>
                <TableCell sx={{ color: "var(--bs-nav)" }}>
                  {index + 1 + (page - 1) * rowsPerPage}
                </TableCell>
                <TableCell sx={{ color: "var(--bs-nav)" }}>
                  {" "}
                  {order?.createdAt &&
                    new Date(order.createdAt).toLocaleString("en-IN")}
                </TableCell>
                <TableCell sx={{ color: "var(--bs-nav)" }}>
                  {order?.n_value}
                </TableCell>
                <TableCell sx={{ color: "var(--bs-nav)" }}>
                  {order?.n_value >= 50000 ? "YES" : "No"}
                </TableCell>
                <TableCell sx={{ color: "var(--bs-nav)" }}>
                  {order?.order_id}
                </TableCell>
                <TableCell sx={{ color: "var(--bs-nav)" }}>
                  {order?.orderStatus}
                </TableCell>
                <TableCell align="center">
                  <Button
                    variant="contained"
                    color="success"
                    onClick={() => handleShip(order)}
                    sx={{
                      borderColor: "var(--bs-primary1)", // Custom colors

                      backgroundColor: "var(--bs-primary1)", // Custom colors
                      color: "white",
                    }}
                  >
                    Ship
                  </Button>
                  <IconButton onClick={(e) => handleMenuClick(e, order)}>
                    <MoreVertIcon />
                  </IconButton>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        );
      case "Manifested":
        if (!orderList?.length) return renderNoDataRow(11);
        return (
          <TableBody>
            {orderList?.map((order, index) => (
              <TableRow key={index} hover>
                <TableCell sx={{ color: "var(--bs-nav)" }}>
                  {index + 1 + (page - 1) * rowsPerPage}
                </TableCell>
                <TableCell sx={{ color: "var(--bs-nav)", minWidth: "200px" }}>
                  {" "}
                  {order?.createdAt &&
                    new Date(order.createdAt).toLocaleString("en-IN")}
                </TableCell>
                <TableCell sx={{ color: "var(--bs-nav)", minWidth: "150px" }}>
                  {order?.n_value}
                </TableCell>
                <TableCell sx={{ color: "var(--bs-nav)", minWidth: "150px" }}>
                  {order?.order_id}
                </TableCell>
                <TableCell sx={{ color: "var(--bs-nav)", minWidth: "150px" }}>
                  {order?.courier_name}
                </TableCell>
                <TableCell sx={{ color: "var(--bs-nav)", minWidth: "150px" }}>
                  <NavLink
                    to={`/order-details/${order?.shipRocket_order_id}/${order?.waybill}`}
                    style={{
                      textDecoration: "underline",
                      color: "var(--bs-primary1)",
                    }}
                  >
                    {order?.waybill}
                  </NavLink>
                </TableCell>

                <TableCell sx={{ color: "var(--bs-nav)", minWidth: "150px" }}>
                  {order?.finalAmount}
                </TableCell>
                <TableCell sx={{ color: "var(--bs-nav)", minWidth: "150px" }}>
                  {order?.orderStatus}
                </TableCell>
                <TableCell sx={{ minWidth: "300px", textAlign: "center" }}>
                  <Button
                    variant="contained"
                    color="success"
                    onClick={() => genrateMenifest(order)}
                    sx={{
                      borderColor: "var(--bs-primary1)", // Custom colors

                      backgroundColor: "var(--bs-primary1)", // Custom colors
                      color: "white",
                    }}
                  >
                    Generate Manifeast
                  </Button>
                  <IconButton onClick={(e) => handleMenuClick(e, order)}>
                    <MoreVertIcon />
                  </IconButton>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        );
      case "Ready To Ship":
        if (!orderList?.length) return renderNoDataRow(11);
        return (
          <TableBody>
            {orderList?.map((order, index) => (
              <TableRow key={index} hover>
                <TableCell sx={{ color: "var(--bs-nav)" }}>
                  {index + 1 + (page - 1) * rowsPerPage}
                </TableCell>
                <TableCell sx={{ color: "var(--bs-nav)", minWidth: "200px" }}>
                  {" "}
                  {order?.createdAt &&
                    new Date(order.createdAt).toLocaleString("en-IN")}
                </TableCell>
                <TableCell sx={{ color: "var(--bs-nav)", minWidth: "150px" }}>
                  {order?.n_value}
                </TableCell>
                <TableCell sx={{ color: "var(--bs-nav)", minWidth: "150px" }}>
                  {order?.order_id}
                </TableCell>
                <TableCell sx={{ color: "var(--bs-nav)", minWidth: "150px" }}>
                  {order?.courier_name}
                </TableCell>
                <TableCell sx={{ color: "var(--bs-nav)", minWidth: "150px" }}>
                  <NavLink
                    to={`/order-details/${order?.shipRocket_order_id}/${order?.waybill}`}
                    style={{
                      textDecoration: "underline",
                      color: "var(--bs-primary1)",
                    }}
                  >
                    {order?.waybill}
                  </NavLink>
                </TableCell>

                <TableCell sx={{ color: "var(--bs-nav)", minWidth: "150px" }}>
                  {order?.finalAmount}
                </TableCell>
                <TableCell sx={{ color: "var(--bs-nav)", minWidth: "150px" }}>
                  {order?.orderStatus}
                </TableCell>
                <TableCell sx={{ minWidth: "250px", textAlign: "center" }}>
                  <Button
                    variant="contained"
                    color="success"
                    onClick={() => secdulePickup(order)}
                    sx={{
                      borderColor: "var(--bs-primary1)", // Custom colors

                      backgroundColor: "var(--bs-primary1)", // Custom colors
                      color: "white",
                    }}
                  >
                    Schedule Pickup
                  </Button>
                  <IconButton onClick={(e) => handleMenuClick(e, order)}>
                    <MoreVertIcon />
                  </IconButton>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        );
      case "delivered":
        if (!orderList?.length) return renderNoDataRow();
        return (
          <TableBody>
            {orderList?.map((order, index) => (
              <TableRow key={index} hover>
                <TableCell sx={{ color: "var(--bs-nav)" }}>
                  {index + 1 + (page - 1) * rowsPerPage}
                </TableCell>
                <TableCell sx={{ color: "var(--bs-nav)" }}>
                  {order?.order_id}
                </TableCell>
                <TableCell sx={{ color: "var(--bs-nav)" }}>
                  {order?.deliveredDate}
                </TableCell>
                <TableCell sx={{ color: "var(--bs-nav)" }}>
                  {order?.deliveryPerson}
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        );
      case "Canceled":
        if (!orderList?.length) return renderNoDataRow(5);
        return (
          <TableBody>
            {orderList?.map((order, index) => (
              <TableRow key={index} hover>
                <TableCell sx={{ color: "var(--bs-nav)" }}>
                  {index + 1 + (page - 1) * rowsPerPage}
                </TableCell>
                <TableCell sx={{ color: "var(--bs-nav)", minWidth: "200px" }}>
                  {" "}
                  {order?.createdAt &&
                    new Date(order.createdAt).toLocaleString("en-IN")}
                </TableCell>
                <TableCell sx={{ color: "var(--bs-nav)", minWidth: "150px" }}>
                  {order?.n_value}
                </TableCell>
                <TableCell sx={{ color: "var(--bs-nav)", minWidth: "150px" }}>
                  {order?.order_id}
                </TableCell>
                <TableCell sx={{ color: "var(--bs-nav)", minWidth: "150px" }}>
                  {order?.orderStatus}
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        );

      default:
        return null;
    }
  };

  const redirectOrderDetailsPage = () => {
    const basePath = `/order-details/${selectedOrder?.shipRocket_order_id}`;
    const fullPath = selectedOrder?.waybill
      ? `${basePath}/${selectedOrder?.waybill}`
      : basePath;
    navigate(fullPath);
  };

  return (
    <>
      {loading && <Loader />}
      <Paper
        sx={{
          padding: 3,
          borderRadius: 2,
          // border: "1px solid #ccc",

          // background: "linear-gradient(135deg,  #F87A1F ,#FFCC29)",
          boxShadow: "0 4px 12px rgba(0, 0, 0, 0.1)",
        }}
      >
        <Typography
          variant="h6"
          sx={{
            fontWeight: "600",
            fontSize: "24px",
            //  color: "var(--bs-nav)",
            // textAlign: "center",
          }}
        >
          Order Details
        </Typography>
      </Paper>

      <div className="card">
        <div className="card-header"> {/* <span>Filter</span> */}</div>
        <div className="card-body">
          <form>
            <div className="form-row" style={{ alignItems: "end" }}>
              <div className="form-group col-md-4">
                <label>Start Date</label>
                <input
                  type="date"
                  name="startDate"
                  className="form-control"
                  value={filters.startDate}
                  onChange={handleChange}
                  max={today}
                />
              </div>
              <div className="form-group col-md-4">
                <label>End Date</label>
                <input
                  type="date"
                  name="endDate"
                  className="form-control"
                  value={filters.endDate}
                  onChange={handleChange}
                  max={today}
                />
              </div>
              <div className="form-group col-md-4">
                <label>Order ID</label>
                <input
                  type="text"
                  name="orderId"
                  placeholder="Order ID"
                  className="form-control"
                  value={filters.orderId}
                  onChange={handleChange}
                />
              </div>

              <div className="form-group col-md-6 col-lg-4">
                <label>&nbsp;</label>
                <div>
                  <button
                    type="button"
                    className="btn btn-secondary mr-3"
                    onClick={handleSubmit}
                  >
                    Search
                  </button>
                  <button
                    type="button"
                    className="btn btn-light"
                    onClick={handleReset}
                  >
                    Reset
                  </button>
                </div>
              </div>
            </div>
          </form>
        </div>
      </div>

      <Box
        sx={{ width: "100%", borderBottom: 1, borderColor: "divider", mt: 2 }}
      >
        <Tabs
          value={activeStatus}
          onChange={handleStatusChange}
          variant="scrollable"
          scrollButtons="auto"
          aria-label="Order Status Tabs"
          sx={{
            "& .MuiTab-root": {
              textTransform: "none",
              fontWeight: 500,
              minWidth: 120,
              color: "#555",
              "&.Mui-selected": {
                color: "#000000",
                fontWeight: "bold",
              },
            },
            "& .MuiTabs-indicator": {
              backgroundColor: "#000000",
            },
          }}
        >
          {Object.entries(statusFilters).map(([key, label]) => (
            <Tab key={key} label={label} value={key} />
          ))}
        </Tabs>
      </Box>

      <Paper
        sx={{
          padding: 3,
          borderRadius: 2,
          // border: "1px solid #ccc",
          marginTop: 4,
          // background: "linear-gradient(135deg,  #F87A1F ,#FFCC29)",
          boxShadow: "0 4px 12px rgba(0, 0, 0, 0.1)",
        }}
      >
        <Box sx={{ padding: 1 }}>
          {/* <Box display={'flex'} justifyContent={'flex-end'}>
        <Button variant='contained' color="error" sx={{ width: "200px" , marginBottom:2 ,     borderColor: 'var(--bs-primary1)', // Custom colors
                              
                              backgroundColor: 'var(--bs-nav)', 
                              color: "white", }} >
               DownLoad Excel
             </Button>
        </Box> */}

          <TableContainer component={Box} elevation={3}>
            <Table>
              <TableHead sx={{ backgroundColor: "#f5f5f5" }}>
                {renderTableHeader()}
              </TableHead>

              {renderTableContent()}
            </Table>
            <Menu
              anchorEl={anchorEl}
              open={open}
              onClose={handleClose}
              anchorOrigin={{ vertical: "bottom", horizontal: "right" }}
              transformOrigin={{ vertical: "top", horizontal: "right" }}
              PaperProps={{
                sx: {
                  borderRadius: 2,
                  minWidth: 200,
                  bgcolor: "#fff",
                  boxShadow: "0px 4px 20px rgba(0, 0, 0, 0.1)",
                  p: 1,
                },
              }}
              MenuListProps={{
                sx: {
                  py: 0,
                },
              }}
            >
              {activeStatus === "New" && (
                <MenuItem
                  onClick={() => {
                    /* handle manifest logic */ handleCancelClick();
                  }}
                  sx={{
                    fontSize: "14px",
                    // color: '#333',
                    color: "red",
                    px: 2,
                    py: 1,
                    // '&:hover': {
                    //   bgcolor: '#f5f5f5',
                    //   color: '#1976d2',
                    // },
                  }}
                >
                  Cancel Order
                </MenuItem>
              )}

              <MenuItem
                onClick={() => {
                  /* handle invoice logic */ genrateInvoice();
                }}
                sx={{
                  fontSize: "14px",
                  color: "var(--bs-nav)",
                  px: 2,
                  py: 1,
                  // '&:hover': {
                  //   bgcolor: '#f5f5f5',
                  //   color: '#1976d2',
                  // },
                }}
              >
                Download Invoice
              </MenuItem>

              <MenuItem
                onClick={() => {
                  /* handle label logic */ genrateLabel();
                }}
                sx={{
                  fontSize: "14px",
                  // color: '#333',
                  color: "var(--bs-nav)",
                  px: 2,
                  py: 1,
                  // '&:hover': {
                  //   bgcolor: '#f5f5f5',
                  //   color: '#1976d2',
                  // },
                }}
              >
                Generate Label
              </MenuItem>

              <MenuItem
                onClick={() => {
                  /* handle manifest logic */ redirectOrderDetailsPage();
                }}
                sx={{
                  fontSize: "14px",
                  // color: '#333',
                  color: "var(--bs-nav)",
                  px: 2,
                  py: 1,
                  // '&:hover': {
                  //   bgcolor: '#f5f5f5',
                  //   color: '#1976d2',
                  // },
                }}
              >
                View Order
              </MenuItem>
            </Menu>
          </TableContainer>

          {/* Pagination + Rows Per Page */}
          <Box
            display="flex"
            justifyContent="space-between"
            alignItems="center"
            mt={2}
            flexWrap="wrap"
            gap={2}
          >
            <Box display="flex" alignItems="center" gap={2}>
              <FormControl size="small" sx={{ width: "150px" }}>
                <InputLabel>Rows per page</InputLabel>
                <Select
                  value={rowsPerPage}
                  onChange={handleChangeRowsPerPage}
                  label="Rows per page"
                  sx={{
                    fontSize: "14px",
                    "& .MuiOutlinedInput-notchedOutline": {
                      borderColor: "#ccc",
                    },
                    "&:hover .MuiOutlinedInput-notchedOutline": {
                      borderColor: "var(--bs-primary1)",
                    },
                    "&.Mui-focused .MuiOutlinedInput-notchedOutline": {
                      borderColor: "var(--bs-primary1)",
                    },
                  }}
                >
                  {[5, 10, 25, 50].map((num) => (
                    <MenuItem key={num} value={num}>
                      {num}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>

              {/* Total Entries Count */}
              <Typography
                variant="body2"
                sx={{ fontSize: "14px", color: "#555" }}
              >
                Total Entries: {totalData}
              </Typography>
            </Box>

            <Pagination
              count={totalPages}
              page={page}
              onChange={handleChangePage}
              // color="primary"
              shape="rounded"
              showFirstButton
              showLastButton
              sx={{
                "& .MuiPaginationItem-root": {
                  color: "var(--bs-primary1)",
                  borderRadius: "8px",
                },
                "& .MuiPaginationItem-root.Mui-selected": {
                  backgroundColor: "var(--bs-primary1)",
                  color: "#fff",
                  fontWeight: "bold",
                  "&:hover": {
                    backgroundColor: "var(--bs-primary1)",
                  },
                },
              }}
            />
          </Box>
        </Box>
      </Paper>
      <Dialog open={openDialog} onClose={handleDialogClose}>
        <DialogTitle sx={{ fontWeight: 600, color: "var(--bs-nav)" }}>
          Confirm Cancellation
        </DialogTitle>
        <DialogContent>
          <Typography variant="body2" sx={{ fontSize: 14 }}>
            Are you sure you want to cancel this order? This action cannot be
            undone.
          </Typography>
        </DialogContent>
        <DialogActions sx={{ px: 3, pb: 2 }}>
          <Button onClick={handleDialogClose} color="inherit">
            No, Keep Order
          </Button>
          <Button
            onClick={cancelOrder}
            color="error"
            variant="contained"
            sx={{ backgroundColor: "red", color: "white" }}
          >
            Yes, Cancel It
          </Button>
        </DialogActions>
      </Dialog>
    </>
  );
};

export default OrderShowTable;
